import { Routes, Route } from "react-router-dom";
import Contacts from "./containers/contacts/Contacts";
import Header from "./components/Header/Header";
import NewContact from "./containers/newContact/NewContact";
import { useEffect, useReducer, useState } from "react";
import reducer from "./utils/reducer";
import { MainContext } from "./context/MainContext";
import EditContact from "./containers/editContact/EditContact";
import ScaleLoader from "react-spinners/ScaleLoader";
function App() {
  let [loading, setLoading] = useState(true);

  const [connections, dispatch] = useReducer(reducer, [], () => {
    const localConnections = localStorage.getItem("Connections");

    return localConnections ? JSON.parse(localConnections) : [];
  });

  useEffect(() => {
    setTimeout(()=>{
      setLoading(false)
    },2000)
    localStorage.setItem("Connections", JSON.stringify(connections));
  }, [connections]);
  return (
    <>
     { loading?
      <div className="loading">
        <ScaleLoader color="#5dfd5d" loading={loading} size={150} />
      </div>
      :
      <div className="App">
        <Header />
        <MainContext.Provider value={{ connections, dispatch }}>
          <Routes>
            <Route path="/Contacts" element={<Contacts />} />
            <Route path="/Contacts/new" element={<NewContact />} />
            <Route
              path="/Contacts/edit/:id"
              element={<EditContact connections={connections} />}
            />
          </Routes>
        </MainContext.Provider>
      </div>}
    </>
  );
}

export default App;
