import React from 'react'
import { useParams } from 'react-router-dom'
import ContactForm from '../../components/ContactForm/ContactForm'
import './EditContact.scss'
function EditContact({ connections }) {
  const { id } = useParams()
  //Dyiiklik edilck laq CurrentConnection
  let CurrentConnection = connections.find((Person) => Person.id === id)
  return (
    <div className="EditContact">
      <h1>laq zrind dyiiklik</h1>
      <ContactForm CurrentConnection={CurrentConnection} />
    </div>
  )
}

export default EditContact
