// trafli mlumat funksiyasi
import { Modal} from 'antd';

function info(id,connections) {

  let CurrentPerson=connections.find(Person=>Person.id===id)
  Modal.info({
    title: 'trafl Mlumat',
    content: (
      <div>
        <p><span className='Bold'>Ad :</span> {CurrentPerson.firstName}</p>
        <p><span className='Bold'>Soyad :</span> {CurrentPerson.lastName}</p>
        <p><span className='Bold'>Ata ad :</span> {CurrentPerson.fatherName}</p>
        <p><span className='Bold'>Email :</span> {CurrentPerson.email}</p>
        <p><span className='Bold'>Ixtisas :</span> {CurrentPerson.position}</p>
        <p><span className='Bold'>Cins :</span> {CurrentPerson.gender}</p>
        <p><span className='Bold'>Yenilnm haqqinda mlumat istyirmi :</span> {CurrentPerson.Subscription? "H":"Yox"}</p>
        <p><span className='Bold'>lav mlumat :</span> {CurrentPerson.Additional_Info}</p>
     
      </div>
    ),
    onOk() {},
  });
}

export default info