import React from 'react'
import { Form, Input, Button, Checkbox, Select, Radio } from 'antd'
import { MainContext } from '../../context/MainContext'
import { useContext } from 'react'
import { v4 as uuidv4 } from 'uuid'
import { toast, ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { useNavigate } from 'react-router-dom'
import './ContactForm.scss'
function ContactForm({ CurrentConnection }) {
  const { Option } = Select

  const { dispatch } = useContext(MainContext)
  let navigate = useNavigate()
  const [form] = Form.useForm()

  //laq zrind dzli olan halda aadak funksiya ilyck
  const Editconnection = (value, id) => {
    value.id = id
    if (JSON.stringify(CurrentConnection) !== JSON.stringify(value)) {
      dispatch({ type: 'Remove_Connection', payload: id })
      dispatch({ type: 'Add_Connection', payload: value })
      const resolveAfter3Sec = new Promise((resolve) =>
        setTimeout(resolve, 2000),
      )
      toast.promise(resolveAfter3Sec, {
        pending: 'Dyiiklr qeyd alnr',
        success: 'Qeyd alnd, sas Shify Ynlndirilirsiniz ',
      })
      setTimeout(() => {
        navigate('/Contacts')
      }, 3000)
    } else {
      toast.error('Error! Dyiiklik yoxdur', {
        position: 'top-center',
        autoClose: 1100,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
      })
    }
  }

  // laq lav olunanda bu funksiya ilyck
  const SaveConnection = (value) => {
    value.id = uuidv4()
    dispatch({ type: 'Add_Connection', payload: value })
    const resolveAfter3Sec = new Promise((resolve) => setTimeout(resolve, 2000))
    toast.promise(resolveAfter3Sec, {
      pending: 'laq lav olunur',
      success: 'lav olundu, sas Shify Ynlndirilirsiniz ',
    })
    setTimeout(() => {
      navigate('/Contacts')
    }, 3000)
  }

  return (
    <>
      <ToastContainer hideProgressBar position="top-center" />

      <Form
        name="basic"
        form={form}
        labelCol={{
          span: 9,
        }}
        wrapperCol={{
          span: 6,
        }}
        initialValues={
          //gr zrind dyiiklik edilck laq varsa , laqnin ilkin datas fieldlr yazilir,
          //yoxdursa (yni ilk df lav olunarsa) default olaraq aadak dyrlr yazlr
          CurrentConnection?
             {
                firstName: CurrentConnection.firstName,
                lastName: CurrentConnection.lastName,
                fatherName: CurrentConnection.fatherName,
                email: CurrentConnection.email,
                position: CurrentConnection.position,
                Additional_Info: CurrentConnection.Additional_Info,
                gender: CurrentConnection.gender,
                Subscription: CurrentConnection.Subscription,
              }
            : {
                position: 'Vzif 1',
                gender: 'Kii',
                Subscription: false,
              }
        }
        autoComplete="off"
        onFinish={(value) => {
          //gr zrind dyiiklik edilck laq varsa , Editconnection funksiyasi laq zrind dyiiklik edir,
          //yoxdursa SaveConnection funkisyasi onu laqlr lav edir
          CurrentConnection?
             Editconnection(value, CurrentConnection.id)
            : SaveConnection(value)

          form.resetFields() // formu sfrlayr
        }}
      >
        <Form.Item
          label="Ad"
          name="firstName"
          rules={[
            {
              required: true,
              message: 'Zhmt olmasa adnz daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Adnz daxil edin" />
        </Form.Item>

        <Form.Item
          label="Soyad"
          name="lastName"
          rules={[
            {
              required: true,
              message: 'Zhmt olmasa soyadnz daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Soyadnz daxil edin" />
        </Form.Item>

        <Form.Item
          label="Ata ad"
          name="fatherName"
          rules={[
            {
              required: true,
              message: 'Zhmt olmasa atanzn adn daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Atanzn adn daxil edin" />
        </Form.Item>

        <Form.Item
          label="Email"
          name="email"
          rules={[
            {
              type: 'email',
              message: 'Emailinizi shvdir!',
            },
            {
              required: true,
              message: 'Zhmt olmasa emailinizi daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Emailinizi daxil edin" />
        </Form.Item>

        <Form.Item
          name="Additional_Info"
          label="lav Mlumat"
          rules={[
            {
              required: true,
              message: 'Zhmt olmasa lav mlumat daxil edin!',
            },
          ]}
        >
          <Input.TextArea />
        </Form.Item>

        <Form.Item label="Vzif" name="position">
          <Select>
            <Option value="Vzif 1">Vzif 1</Option>
            <Option value="Vzif 2">Vzif 2</Option>
            <Option value="Vzif 3">Vzif 3</Option>
            <Option value="Vzif 4">Vzif 4</Option>
          </Select>
        </Form.Item>

        <Form.Item label="Cins" name="gender">
          <Radio.Group>
            <Radio value={'Kii'}>Kii</Radio>
            <Radio value={'Qadn'}>Qadn</Radio>
            <Radio value={'Digr'}>Digr</Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item
          name="Subscription"
          valuePropName="checked"
          wrapperCol={{
            md: {
              offset: 9,
              span: 16,
            },
          }}
        >
          <Checkbox>Yeniliklr bard mlumat bal mlumat ver </Checkbox>
        </Form.Item>

        <Form.Item
          wrapperCol={{
            md: {
              offset: 9,
              span: 16,
            },
          }}
        >
          <Button className="Addbtn" htmlType="submit">
            Tsdiql
          </Button>
        </Form.Item>
      </Form>
    </>
  )
}

export default ContactForm
