import React from 'react'
import { Table } from 'antd'
import './ContactTable.scss'
import info from '../../utils/InfoModal'
import showConfirm from '../../utils/DeleteConnection'
import { Link } from 'react-router-dom'
function ContactTable({ connections, dispatch }) {
  const columns = [
    {
      title: 'Ad',
      dataIndex: 'firstName',
      width: 150,
    },
    {
      title: 'Soyad',
      dataIndex: 'lastName',
      width: 150,
    },
    {
      title: 'Ata ad',
      dataIndex: 'fatherName',
    },
    {
      title: 'Ixtisas',
      dataIndex: 'position',
    },
    {
      title: 'mliyyatlar',
      dataIndex: 'operations',
      render: (_, record) => (
        // burada record laqnin zdr v record.key laqnin id-dir
        <div>
          <span className="clear" onClick={() => info(record.key, connections)}>
            <i className="fa-solid fa-circle-info"></i>
          </span>

          <Link className="clear" to={`/Contacts/edit/${record.key}`}>
            <i className="fa-solid fa-pen-to-square"></i>
          </Link>

          <span
            className="clear"
            onClick={() => showConfirm(record.key, data, dispatch)}
          >
            <i className="fa-solid fa-trash-can"></i>
          </span>
        </div>
      ),
    },
  ]

  let data = []
  // makeRow funksiyasi connection massivindn laqlri data massivin daxil edir,
  // data massivid cedvelde gstrilir
  const makeRow = () => {
    connections.map((Person) =>
      data.push({
        key: Person.id,
        firstName: Person.firstName,
        lastName: Person.lastName,
        fatherName: Person.fatherName,
        position: Person.position,
      }),
    )
  }
  makeRow()

  return (
    <div>
      <Table
        columns={columns}
        dataSource={data}
        pagination={data.length > 11 ? true : false}
      />
    </div>
  )
}

export default ContactTable
